<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="6">
        <p><strong>Nazwa</strong>: {{ row.name }}<span v-if="row.nameOther"> ({{ row.nameOther }})</span> </p>
        <p><strong>Nazwa łacińska</strong>: <span style="font-style:italic;">{{ row.nameLatin }}</span></p>
        <p><strong>Wartość</strong>: {{ getValueDescription(row.lwu) }}</p>
        <p><strong>Kwiatostan</strong>: {{ row.inflorescences.join(', ') }}</p>
        <p v-if="row.detailsInflorescence">{{ row.detailsInflorescence }}</p>
      </el-col>
      <el-col :span="12">
        <p v-if="row.details"><strong>Informacje</strong>: {{ row.details }}</p>
        <p v-if="row.humidity"><strong>Wilgotność siedlisk</strong>: {{ row.humidity.join(', ') }}</p>
        <p v-if="row.detailsHabitat"><strong>Występowanie</strong>: {{ row.detailsHabitat }}</p>
        <p v-if="row.detailsRemoval"><strong>Zwalczanie</strong>: {{ row.detailsRemoval }}</p>
        <el-tag v-if="row.heightTypes" type="gray" v-for="heightType in row.heightTypes" :key="heightType" close-transition>{{ heightType }}</el-tag>
        <el-tag v-if="row.propagations" type="success" v-for="propagation in row.propagations" :key="propagation" close-transition>{{ propagation }}</el-tag>
        <el-tag v-if="row.weeds" :type="weed == 'chwast bezwzględny' ? 'danger' : 'warning'" v-for="(weed, index) in row.weeds" :key="index" close-transition>{{ weed }}</el-tag>
        <el-tag v-if="row.indicators" v-for="indicator in row.indicators" :key="indicator" close-transition>{{ indicator }}</el-tag>
      </el-col>
      <el-col :span="6">
        <table class="el-table el-table--border el-table--fit">
          <colgroup>
            <col width="33%"/>
            <col width="33%"/>
            <col/>
          </colgroup>
          <thead>
            <tr>
              <th style="text-align:center;">Łąki</th>
              <th style="text-align:center;">Pastwiska</th>
              <th style="text-align:center;">Krótkotrwałe</th>
            </tr>
          </thead>
          <tbody>
            <tr class="el-table__row">
              <td style="text-align:center;"><el-rate :value="row.meadowsApplicable" :max="2" :colors="['#666666']" disabled></el-rate></td>
              <td style="text-align:center;"><el-rate :value="row.pasturesApplicable" :max="2" :colors="['#666666']" disabled></el-rate></td>
              <td style="text-align:center;"><el-rate :value="row.shortUsagesApplicable" :max="2" :colors="['#666666']" disabled></el-rate></td>
            </tr>
          </tbody>
        </table>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'species-details',
  props: {
    row: Object
  },
  methods: {
    getValueDescription (lwu) {
      if (lwu > 8) {
        return 'bardzo dobra'
      } else if (lwu > 6) {
        return 'dobra'
      } else if (lwu > 3) {
        return 'średnia'
      } else {
        return 'niska'
      }
    }
  }
}
</script>



